.header {
    padding: 20px 0;
    box-shadow: 0 2px 10px 0 rgba(0, 98, 199, 0.04);
    background: var(--shrift---belyy---100);
    margin-bottom: 40px;

    &__row {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
    }
}

.user {
    border: 1px solid var(--vtorostepennyy---400);
    border-radius: 50px;
    padding: 12px 20px 12px 16px;
    display: flex;
    align-items: center;
    gap: 12px;
    color: var(--shrift---temnyy---100);

    &__photo {
        display: block;
        min-width: 40px;
        width: 40px;
        height: 40px;
        border-radius: 100%;
        overflow: hidden;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}