.settings {
    &__top {
        margin-bottom: 52px;
    }

    &__form {
        display: flex;
        align-items: flex-start;
        gap: 52px;

        &_photo {
            width: 100%;
            min-width: 160px;
            max-width: 160px;
            height: 160px;
            position: relative;
        }
        
        &_img {
            display: block;
            border-radius: 200px;
            width: 100%;
            height: 100%;
            background: var(--vtorostepennyy---500);
            overflow: hidden;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &_add {
            position: absolute;
            right: -5px;
            bottom: -5px;
            & svg {
                transition: all 0.3s ease;
            }
            &:hover svg {
                fill: var(--osnovnoy---500);
            }
        }

        &_inner {
            display: flex;
            gap: 32px;
            flex-wrap: wrap;
        }

        &_input {
            flex: 1 0 45%;
        }

        &_btn {
            margin-top: 10px;
        }
    }
}

@media(max-width: 1260px) {
    .settings {
        &__form {
            &_input {
                flex: 1 0 100%;
            }
        }
    }
}