/* swiper CUSTOM */

.swiper {
    width: 100%;
    height: 100%;
}

/* swiper-buttons */
.swiper-btns {
    display: flex;
    align-items: center;
    gap: 6px;
}
.swiper-button-next, .swiper-button-prev {
    position: static;
    margin: 0;
    padding: 13px;
    min-width: 40px;
    width: 40px;
    height: 40px;
    border-radius: 25px;
    background: var(--belyy---20);
    opacity: 1;
    transition: all 0.3s ease;
    & svg path {
        transition: all 0.3s ease;
    }
}
.swiper-button-next:after, .swiper-button-prev:after {
    content: none;
}
.swiper-button-next:hover,.swiper-button-prev:hover {
    background: var(--belyy---100);
}
.swiper-button-next:hover svg path, 
.swiper-button-prev:hover svg path {
    fill: var(--osnovnoy---500);
}


/* swiper-buttons--white */
.swiper-button-next--white, .swiper-button-prev--white {
    background: var(--belyy);
}
.swiper-button-next--white:after, .swiper-button-prev--white:after {
    color: var(--color-temno-zelenyy);
}
.swiper-button-next--white:hover,.swiper-button-prev--white:hover {
    background: rgba(61, 124, 133, 0.25);
}
.swiper-button-next--white:hover:after, .swiper-button-prev--white:hover:after {
    color: var(--color-temno-zelenyy);
}


/* swiper-pagination  */
.swiper-pagination {
    position: static;
    transform: none !important;
    width: auto !important;

    &-bullet {
        position: static;
        border-radius: 100%;
        width: 10px;
        height: 10px;
        background: var(--belyy---100);
        margin: 8px 0 !important;

        &-active {
            position: relative;
            margin: 13px 0 !important;
            &::after {
                content: "";
                display: block;
                width: 20px;
                height: 20px;
                border-radius: 100%;
                border: 1px solid var(--belyy---100);
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }
    }
}

.swiper-pagination-fraction {
    font-weight: 700;
    font-size: 14px;
    color: rgba(255, 255, 255, 0.5);

    .swiper-pagination-current {
        font-weight: 700;
        font-size: 26px;
        color: var(--belyy);
    }
}

.swiper-scrollbar {
    position: static !important;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
    &-drag {
        background: var(--belyy);
        border-radius: 10px;
    }
}
.swiper-scrollbar-temnyy {
    position: static !important;
    background: var(--izobrazhenie);
    .swiper-scrollbar-drag {
        background: var(--color-zelenyy);
    }
}
