.text-block {
    color: var(--shrift---temnyy---tekst-v-uroke);

    & h1, & h2, & h3, & h4, & h5, & h6 {
        font-family: var(--font-family);
        font-weight: 700;
        line-height: 120%;
        color: var(--shrift---temnyy---100);
        margin-bottom: 24px;
        &:not(:first-child) {
            margin-top: 40px;
        }
    }
    & h1 {
        font-size: 46px;
    }
    & h2 {
        font-size: 40px;
    }
    & h3 {
        font-size: 32px;
    }
    & h4 {
        font-size: 24px;
    }
    & h5 {
        font-size: 20px;
    }

    & p {
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }

    & span {
        color: var(--osnovnoy---500);
    }

    & a {
        text-decoration: underline;
        color: var(--osnovnoy---500);
    }

    & b {
        font-weight: 700;
    }

    & ul:not(.breadcrumbs) {
        display: flex;
        flex-direction: column;
        gap: 12px;
        margin-bottom: 20px;
        & > li {
            position: relative;
            padding-left: 28px;
            &::before {
                content: "";
                display: block;
                min-width: 16px;
                width: 16px;
                height: 4px;
                border-radius: 10px;
                background: var(--osnovnoy---500);
                position: absolute;
                top: 9px;
                left: 0;
            }

            &>b {
                display: block;
                font-size: 18px;
                line-height: 140%;
                color: var(--shrift---temnyy---100);
                margin-bottom: 12px;
            }
        }
    }

    & ol {
        margin-bottom: 20px;
        list-style-type: none;
        counter-reset: item;
        & > li {
            font-size: 18px;
            position: relative;
            padding-left: 40px;
            &:not(:last-child) {
                margin-bottom: 24px;
            }
            &:not(:first-child) {
                margin-top: 24px;
            }
            
            &::before {
                content: counter(item);
                counter-increment: item;
                min-width: 28px;
                width: 28px;
                height: 28px;
                border-radius: 20px;
                background: var(--osnovnoy---500);
                font-weight: 500;
                font-size: 14px;
                color: var(--shrift---belyy---100);
                display: flex;
                align-items: center;
                justify-content: center;
                position: absolute;
                top: -2px;
                left: 0;
            }

            &>b {
                display: block;
                font-size: 18px;
                line-height: 140%;
                color: var(--shrift---temnyy---100);
                margin-bottom: 12px;
            }
        }
    }

    & blockquote {
        border: 1px solid rgba(254, 196, 0, 0.5);
        border-radius: 10px;
        padding: 16px 24px;
        background: rgba(254, 196, 0, 0.1);
        color: var(--shrift---temnyy---100);
        padding-left: 51px;
        position: relative;
        &::after {
            content: "";
            display: block;
            width: 15px;
            height: 20px;
            background: url("./../img/icons/lamp.svg") no-repeat center / contain;
            position: absolute;
            top: 17px;
            left: 24px;
        }
    }

    & table {
        //display: block;
        width: 100%;
        outline: 1px solid var(--osnovnoy---100);
        border-radius: 20px;
        overflow: hidden;
        border-collapse: collapse; 
        margin-bottom: 20px;

        & thead {
            & th {
                border: none;
                border-right: 1px solid var(--osnovnoy---100);
                border-bottom: 1px solid var(--osnovnoy---100);
                padding: 12px 16px;
                font-weight: 700;
                font-size: 18px;
                color: var(--shrift---temnyy---100);
                &:first-child {
                    width: 20%;
                }
            }
            & tr th:last-child {
                border-right: none;
            }
        }
        & tbody {
            & th {
                border: none;
                border-right: 1px solid var(--osnovnoy---100);
                border-bottom: 1px solid var(--osnovnoy---100);
                padding: 12px 16px;
                font-weight: 700;
                font-size: 18px;
                color: var(--shrift---temnyy---100);
                text-align: left;
                height: 68px;
            }
            & tr:last-child th {
                border-bottom: none;
            }

            & td {
                border: none;
                border-right: 1px solid var(--osnovnoy---100);
                border-bottom: 1px solid var(--osnovnoy---100);
                vertical-align: top;
                padding: 12px 16px;
                color: var(--shrift---temnyy---tekst-v-uroke);
                text-align: center;
                vertical-align: middle;
                width: fit-content;
                &.fill {
                    background: var(--osnovnoy---50);
                    color: var(--shrift---temnyy---100);
                    font-weight: 700;
                    font-size: 18px;
                }
            }
            & tr:last-child td {
                border-bottom: none;
            }
            & tr td:last-child {
                border-right: none;
            }
        }
    }

    &__video {
        position: relative;
        width: 100%;
        height: 100%;
        max-height: 570px;
        border-radius: 20px;
        overflow: hidden;
        margin: 30px 0;

        &::before {
            content: "";
            display: block;
            width: 100%;
            height: 100%;
            background-color: rgba(0, 0, 0, 0.3);
            position: absolute;
            top: 0;
            left: 0;
            pointer-events: none;
        }

        &.hide::before {
            display: none;
        }

        & video {
            width: 100%;
            height: 100%;
            max-height: 570px;
            display: block;
            object-fit: cover;
        }

        &_play {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            cursor: pointer;
            z-index: 10;
            transition: all 0.3s ease;
            &:hover {
                transform: translate(-50%, -50%) scale(1.1);
            }
        }
    }

    & picture {
        display: block;
        width: 100%;
        height: 100%;
        border-radius: 20px;
        overflow: hidden;
        //margin-bottom: 20px;
        margin: 30px 0;
        & img {
            width: 100%;
            height: 100%;
            max-height: 570px;
            object-fit: cover;
        }
    }


    & .adaptation {
        &__letters {
            position: relative;
            padding-left: 52px;
        }
        &__letter {
            border-radius: 40px;
            width: 40px;
            height: 40px;
            background: var(--osnovnoy---500);
            font-weight: 700;
            font-size: 20px;
            color: var(--shrift---belyy---100);
            display: flex;
            align-items: center;
            justify-content: center;
            position: absolute;
            top: 50%;
            left: 0;
            transform: translate(0, -50%);
        }

        &__command {
            & ol {
                margin-bottom: 20px;
                list-style-type: none;
                counter-reset: item;
                & > li {
                    &:not(:last-child) {
                        margin-bottom: 28px;
                    }
                    &>b {
                        font-size: 20px;
                        margin-bottom: 16px;
                    }
                }
            }
        }

        &__btn {
            display: inline-block;
            border-radius: 30px;
            padding: 6px 16px;
            font-weight: 500;
            font-size: 16px;
            line-height: 140%;
            color: var(--shrift---belyy---100);
            &.yellow {background: var(--vtorostepennyy---zheltyy);}
            &.green {background: var(--vtorostepennyy---zelenyy);}
            &.gray {background: var(--shrift---temnyy---50);}
            &.red {background: var(--vtorostepennyy---krasnyy);}
            &.blue {background: var(--osnovnoy---500);}
            &.purple {background: var(--vtorostepennyy---fioletovyy);}
        }

        &__cards {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 16px;
        }

        &__card {
            border: 1px solid var(--osnovnoy---100);
            border-radius: 20px;
            padding: 32px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 24px;

            &_fio {
                font-weight: 700;
                font-size: 20px;
                line-height: 110%;
                color: var(--shrift---temnyy---100);
                display: flex;
                align-items: center;
                gap: 12px;
                margin-bottom: 16px;
            }

            & picture {
                max-height: 24px;
                max-width: 24px;
                border-radius: 0;
                overflow: hidden;
                margin: 0;
                & img {
                    width: 100%;
                    height: 100%;
                    max-height: 24px;
                    max-width: 24px;
                    object-fit: cover;
                }
            }

            & a {
                text-decoration: none;
                &:hover {
                    text-decoration: underline;
                }
            }
        }

        &__link {
            border: 1px solid var(--osnovnoy---50);
            border-radius: 30px;
            padding: 12px 24px;
            background: #f0f5fa;
            font-weight: 500;
            font-size: 16px;
            line-height: 140%;
            color: var(--shrift---temnyy---100);
            width: fit-content;
            margin-bottom: 20px;
        }
    }

}