
// courses-teacher
.courses-t {
    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 30px;
        margin-bottom: 32px;

        &_btn {
            border-radius: 40px;
            padding: 8px 8px 8px 24px;
            background: var(--osnovnoy---500);
            color: var(--shrift---belyy---100);
            display: flex;
            align-items: center;
            gap: 16px;
            & svg rect, & svg path {
                transition: all 0.3s ease;
            }
            &:hover svg {
                & rect {
                    fill: var(--shrift---belyy---100);
                    fill-opacity: 1;
                }
                & path {
                    fill: var(--osnovnoy---500);
                }
            }
        }
    }

    &__filter {
        &_btns {
            display: flex;
            gap: 30px;
            border-bottom: 1px solid var(--osnovnoy---50);
            margin-bottom: 52px;
        }

        &_btn {
            border-bottom: 2px solid transparent;
            padding: 0px 0px 16px 0px;
            color: var(--shrift---temnyy---50);
            display: flex;
            align-items: center;
            gap: 10px;
            transition: all 0.3s ease;

            &:hover {
                color: var(--shrift---temnyy---100);
                & .courses-t__filter_count {
                    background: var(--osnovnoy---50);
                    color: var(--osnovnoy---500);
                }
            }

            &.active {
                border-bottom: 2px solid var(--osnovnoy---500);
                color: var(--shrift---temnyy---100);
                & .courses-t__filter_count {
                    background: var(--osnovnoy---50);
                    color: var(--osnovnoy---500);
                }
            }
        }

        &_count {
            border-radius: 30px;
            background: var(--vtorostepennyy---400);
            font-size: 14px;
            text-align: center;
            color: var(--shrift---temnyy---50);
            width: 24px;
            height: 24px;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: all 0.3s ease;
        }
    }

    &__cards {
        display: flex;
        flex-direction: column;
        gap: 16px;
    }

    &__card {
        border: 1px solid rgba(0, 98, 199, 0.05);
        border-radius: 20px;
        padding: 24px;
        background: #f5f9ff;
        
        &_wrap {
            display: flex;
            gap: 28px;
            margin-bottom: 20px;
        }

        &_img {
            display: block;
            border-radius: 10px;
            min-width: 210px;
            width: 210px;
            height: 210px;
            overflow: hidden;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &_inner {
            flex-grow: 1;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 20px;
        }

        &_top {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            gap: 40px;
        }
        
        &_title {
            transition: all 0.3s ease;
        }

        &_links {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            gap: 8px;
            & a {
                font-size: 14px;
                line-height: 140%;
                color: var(--shrift---temnyy---100);
                opacity: 0.5;
                display: flex;
                align-items: center;
                gap: 8px;
                width: max-content;
                transition: all 0.3s ease;
                &:hover {
                    opacity: 1;
                }
                &:first-child::after {
                    content: url("./../img/icons/pen.svg");
                }
                &:last-child::after {
                    content: url("./../img/icons/graph.png");
                }
            }
        }

        &_items {
            display: flex;
            flex-direction: column;
            gap: 10px;
            font-size: 14px;
            line-height: 140%;
            color: var(--shrift---temnyy---50);

            & span {
                color: var(--shrift---temnyy---100);
            }
        }

        &_item {
            display: flex;
            gap: 8px;
            align-items: center;
            & img {
                min-width: 16px;
            }
        }

        &_bottom p {
            font-size: 14px;
            line-height: 140%;
            color: var(--shrift---temnyy---50);
            text-wrap: nowrap;
            margin-bottom: 16px;
            display: flex;
            align-items: center;
            gap: 16px;
            &::after {
                content: "";
                display: block;
                width: 100%;
                height: 1px;
                background-color: var(--osnovnoy---50);
            }
        }

        &_students {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
        }

        &_student {
            display: flex;
            &-photo {
                display: block;
                border: 5px solid #f5f9ff;
                border-radius: 40px;
                width: 49px;
                height: 49px;
                overflow: hidden;
                &:not(:first-child) {
                    margin-left: -18px;
                }
                & img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }
    }
}

