.bottom {
    padding: 160px 0 80px;
    border-radius: 40px;
    margin: 20px;
    background: radial-gradient(166.93% 164.57% at -19.02% 16.17%, #0062c7 0%, #033060 30.93%, #001f3f 71.14%, #0062c7 100%);
    position: relative;
    overflow: hidden;
}

.faq {
    margin-bottom: 160px;

    &__inner {
        display: flex;
        justify-content: space-between;
        gap: 60px;
    }

    &__text {
        font-size: 18px;
        line-height: 140%;
        color: var(--shrift---belyy---50);
        width: 100%;
        max-width: 565px;
        position: relative;
        z-index: 2;
    }

    &__title {
        color: var(--shrift---belyy---100);
        margin-bottom: 24px;
    }

    &__img {
        position: absolute;
        top: 70%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    &__list {
        max-width: 760px;
    }

    &__list ol {
        display: flex;
        flex-direction: column;
        gap: 8px;
        list-style-type: none;
        counter-reset: item;
    }

    &__item {
        border-radius: 20px;
        padding: 24px;
        background: #09315b;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 0 16px;
        transition: all 0.3s ease;

        &::before {
            content: counter(item, decimal-leading-zero);
            counter-increment: item;
            font-size: 14px;
            color: var(--shrift---belyy---80);
            border-radius: 40px;
            padding: 8px 16px;
            background: var(--belyy---20);
            width: 38px;
            height: 38px;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: all 0.3s ease;
        }

        &_quection {
            font-weight: 700;
            font-size: 24px;
            line-height: 140%;
            color: var(--shrift---belyy---100);
            text-align: left;
            flex: 1 0 70%;
            display: flex;
            align-items: center;
            transition: all 0.3s ease;
        }

        & svg path {
            transition: all 0.3s ease;
        }

        & svg{
            transition: all 0.5s ease;
        }

        &_answer {
            font-weight: 500;
            text-align: left;
            color: var(--shrift---temnyy---50);
        }

        &.active {
            background: var(--belyy---100);
            gap: 16px;
            &::before {
                color: var(--shrift---temnyy---50);
                background: var(--vtorostepennyy---400);
            }

            & .faq__item_quection {
                color: var(--osnovnoy---900);
            }
            & .faq__item_btn svg {
                transform: rotate(45deg);
                & path:first-child {
                    fill: var(--osnovnoy---500);
                }
                & path {
                    fill: var(--belyy---100);
                }
            }
        }
    }
}


.footer-i {
    padding-top: 40px;
    border-top: 1px solid var(--belyy---20);

    &__inner {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        padding-bottom: 40px;
        border-bottom: 1px solid var(--belyy---20);
        margin-bottom: 40px;
    }

    &__logo {
        display: block;
        width: fit-content;
        padding: 16px;
        border-radius: 40px;
        background: rgba(255, 255, 255, 0.1);
        margin-bottom: 24px;
        transition: all 0.4s ease;
        &:hover {
            background: rgba(255, 255, 255, 0.2);
        }
    }

    &__info {
        font-size: 14px;
        line-height: 140%;
        color: var(--shrift---belyy---50);
    }

    &__contact {
        flex-basis: 50%;
        display: flex;
        gap: 24px;

        &_text {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 20px;
            font-weight: 700;
            font-size: 20px;
            line-height: 110%;
            color: var(--shrift---belyy---100);
        }

        &_link {
            border-radius: 200px;
            padding: 8px 16px 8px 8px;
            border: 1px solid transparent;
            width: fit-content;
            display: flex;
            align-items: center;
            gap: 10px;
            font-size: 16px;
            color: var(--shrift---belyy---100);
            transition: all 0.3s ease;
            &:hover {
                border: 1px solid var(--shrift---belyy---80);
            }
        }
    }


    &__bottom {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        font-size: 14px;
        color: var(--shrift---belyy---100);
        & a {
            color: var(--shrift---belyy---80);
            text-decoration: underline;
            transition: all 0.3s ease;
            &:hover {
                color: var(--shrift---belyy---100);
            }
        }
    }

}