
/* Общие glob */


html.lock {
    overflow: hidden;
}
html.burger-lock {
    overflow: hidden;
}
html.burger-lock::before {
    opacity: 1;
}
html::before {
    display: block;
    content: "";
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3215686275);
    position: fixed;
    inset: 0;
    opacity: 0;
    z-index: 10;
    pointer-events: none;
    -webkit-transition: opacity 0.3s linear;
    -o-transition: opacity 0.3s linear;
    transition: opacity 0.3s linear;
}
.no-scroll {
    overflow: hidden;
}
body.lock {
    overflow: hidden;
}
body.burger-lock {
    overflow: hidden;
}

.page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

body {
    margin: 0;
    overflow-x: hidden;
    overflow-y: auto;
    padding: 0;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    color: var(--shrift---temnyy---tekst-v-uroke);
    background: #f5f9ff;
}
body.lock {
    overflow: hidden;
}

.container {
	margin: 0 auto;
	padding: 0 var(--container-padding);
	max-width: var(--container-width);
	width: 100%;
}


.dashboard {
    display: flex;
    align-items: flex-start;
    gap: 20px;
    margin-bottom: 160px;
}

.sidebar-wrap {
    max-width: 365px;
    min-height: 500px;
    border-radius: 20px;
    overflow: hidden;
    position: relative;
    transition: all 0.3s ease;

    &__btn {
        display: none;
        position: absolute;
        bottom: 24px;
        left: 50%;
        z-index: 3;
        transform: translate(-50%, 0);
        transition: all 0.3s ease;
        & svg path {
            transition: all 0.3s ease;
        }
        &:hover svg path {
            fill: var(--osnovnoy---500);
        }
        &.rotate {
            display: block;
            transform: translate(-50%, 0) rotate(180deg);
        }
    }

    &.hide {
        &::after {
            display: block;
        }
        & .sidebar-wrap__btn {
            display: block;
            bottom: 24px;
        }
    }

    &::after {
        content: "";
        display: none;
        width: 100%;
        height: 120px;
        background: linear-gradient(360deg, #fff 54.5%, rgba(255, 255, 255, 0.09) 100%);
        position: absolute;
        left: 0;
        bottom: 0;
    }
}
.sidebar {
    border-radius: 20px;
    padding: 24px;
    box-shadow: 0 4px 10px 0 rgba(0, 98, 199, 0.04);
    background: var(--shrift---belyy---100);
    width: 100%;
    margin-bottom: 20px;
    max-width: 365px;

    &__profile {
        display: flex;
        align-items: center;
        gap: 16px;
        padding-bottom: 16px;
        border-bottom: 1px solid var(--vtorostepennyy---400);
        margin-bottom: 16px;

        &_photo {
            display: block;
            min-width: 40px;
            width: 64px;
            height: 64px;
            border-radius: 100%;
            overflow: hidden;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &_name {
            font-size: 20px;
            line-height: 140%;
            color: var(--shrift---temnyy---100);
            margin-bottom: 4px;
        }

        &_post {
            font-size: 14px;
            color: var(--osnovnoy---500);
        }
    }

    &__menu {
        &_link {
            border-radius: 10px;
            padding: 14px 20px;
            font-weight: 500;
            color: var(--shrift---temnyy---100);
            display: flex;
            align-items: center;
            gap: 10px;
            transition: all 0.3s ease;

            & svg path {
                transition: all 0.3s ease;
            }

            &:hover {
                color: var(--osnovnoy---500);
                & svg path {
                    fill: var(--osnovnoy---500);
                }
            }

            &.active {
                background: #f0f5fa;
                color: var(--osnovnoy---500);
                & svg path {
                    fill: var(--osnovnoy---500);
                }
            }
        }

        &_exit {
            padding-top: 16px;
            margin-top: 16px;
            border-top: 1px solid var(--vtorostepennyy---400);
        }
    }
}

.content {
    width: 100%;
    border-radius: 20px;
    padding: 60px;
    box-shadow: 0 4px 10px 0 rgba(0, 98, 199, 0.04);
    background: var(--shrift---belyy---100);
}


/*  titles  */
.title-h1, .title-h2, .title-h3, .title-h4, .title-h5, .title-h6, .title-lk-h1, .title-lk-h2 {
    font-family: var(--font-family);
    font-weight: 700;
    line-height: 120%;
    color: var(--shrift---temnyy---100);
}
.title-h1 {
    font-size: 70px;
}
.title-h2 {
    font-size: 52px;
}
.title-h3 {
    font-size: 32px;
}
.title-h4 {
    font-size: 24px;
}
.title-h5 {
    font-size: 20px;
}

.title-lk-h1 {
    font-size: 46px;
}
.title-lk-h2 {
    font-size: 40px;
}



/*  buttons */
.save-btn {
    display: block;
    border-radius: 40px;
    padding: 16px 24px;
    background: var(--osnovnoy---500);
    color: var(--shrift---belyy---100);
    transition: all 0.3s ease;
    &:hover {
        background: rgba(0, 98, 199, 0.8);
    }
    &:active {
        transform: scale(0.95);
    }
    &:disabled {
        pointer-events: none;
        background: var(--osnovnoy---500);
        opacity: 0.5;
    }
}

.finish-btn {
    border-radius: 40px;
    padding: 8px 8px 8px 32px;
    background: var(--vtorostepennyy---zelenyy);
    color: var(--shrift---belyy---100);
    font-size: 18px;
    display: flex;
    align-items: center;
    gap: 28px;
    transition: all 0.3s ease;

    &:hover {
        background: rgba(32, 202, 137, 0.8);
    }

    &:active {
        transform: scale(0.95);
    }
}
.finish-btn-ok {
    display: block;
    border-radius: 40px;
    padding: 10px;
    width: 48px;
    height: 48px;
    background: rgba(255, 255, 255, 0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease;
    & svg path {
        transition: all 0.3s ease;
    }
}

.white-btn, .blue-btn {
    border-radius: 40px;
    padding: 16px 24px;
    text-align: center;
    transition: all 0.3s ease;
}
.white-btn {
    border: 1px solid var(--osnovnoy---500);
    color: var(--shrift---temnyy---100);
    &:hover {
        color: var(--osnovnoy---500);
    }
}
.blue-btn {
    background: var(--osnovnoy---500);
    color: var(--shrift---belyy---100);
    &:hover {
        background: rgba(0, 98, 199, 0.8);
    }
}


.return-btn, .next-btn, .prev-btn {
    border: 1px solid var(--osnovnoy---50);
    border-radius: 40px;
    padding: 12px 24px;
    color: var(--shrift---temnyy---100);
    display: flex;
    align-items: center;
    gap: 12px;
    transition: all 0.3s ease;
    
    & svg path {
        transition: all 0.3s ease;
    }
}

.return-btn {
    width: fit-content;
    &:hover {
        border: 1px solid var(--osnovnoy---500);
        color: var(--osnovnoy---500);
        & svg path {
            fill: var(--osnovnoy---500);
        }
    }
}

.nav-btns {
    display: flex;
    gap: 12px;
}
.next-btn, .prev-btn {
    background: #f5f9ff;
    &:hover {
        border: 1px solid var(--osnovnoy---500);
    }
    &.disabled {
        pointer-events: none;
        opacity: 0.6;
    }
}
.next-btn::after {
    content: "";
    display: block;
    background-image: url("./../img/icons/arrow-next.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    width: 18px; 
    height: 16px; 
}
.prev-btn::before {
    content: "";
    display: block;
    background-image: url("./../img/icons/arrow-prev.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    width: 18px; 
    height: 16px; 
}


/* tags */
.tag {
    border-radius: 40px;
    padding: 8px 16px 8px 8px;
    display: flex;
    align-items: center;
    gap: 10px;
    position: relative;
    width: fit-content;
    text-wrap: nowrap;

    &::before {
        content: "";
        display: block;
        width: 26px;
        height: 26px;
    }
    
    & span {
        padding-left: 10px;
        border-left: 1px solid var(--belyy---20);
        text-wrap: nowrap;
    }
}

.completed-tag, .new-tag, .process-tag {
    font-weight: 500;
    font-size: 14px;
    color: var(--shrift---belyy---100);
}

.completed-tag {
    background: var(--vtorostepennyy---zelenyy);
    &::before {
        background: url("./../img/icons/tag-completed.svg") no-repeat center / contain;
    }
}
.new-tag {
    background: var(--vtorostepennyy---fioletovyy);
    &::before {
        background: url("./../img/icons/tag-new.svg") no-repeat center / contain;
    }
}
.process-tag {
    background: var(--osnovnoy---300);
    &::before {
        background: url("./../img/icons/tag-process.svg") no-repeat center / contain;
    }
}

.progress {
    & p {
        font-weight: 500;
        font-size: 14px;
        line-height: 140%;
        text-align: right;
        color: var(--shrift---temnyy---100);
        opacity: 0.5;
        text-wrap: nowrap;
        margin-bottom: 8px;
    }
}

.progress-tag, .progress-tag-white {
    padding: 6px 12px 6px 6px;
    gap: 8px;
    font-size: 14px;
    color: var(--shrift---temnyy---50);
    margin-left: auto;
    & span {
        font-size: 18px;
        color: var(--shrift---temnyy---100);
    }
    &__icon {
        padding-right: 8px;
        border-right: 1px solid var(--vtorostepennyy---500);
    }
}

.progress-tag {
    background: var(--vtorostepennyy---500);
    & span {
        border-left: 1px solid rgba(0, 31, 63, 0.07);
    }
    &::before {
        background: url("./../img/icons/tag-progress-1.svg") no-repeat center / contain;
    }
}
.progress-tag-white {
    box-shadow: 0 4px 10px 0 rgba(0, 98, 199, 0.04);
    background: var(--shrift---belyy---100);
    & span {
        border-left: 1px solid var(--vtorostepennyy---500);
    }
    &::before {
        background: url("./../img/icons/tag-progress.svg") no-repeat center / contain;
    }
}


.btn-circle {
    display: block;
    border-radius: 200px;
    width: 34px;
    min-width: 34px;
    height: 34px;
    background: var(--osnovnoy---500);
    color: var(--belyy);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease;

    &::after {
        content: url("./../img/icons/arrow-white.svg");
    }
    &:hover {
        opacity: 0.5;
    }
}

.btn-more.active {
    &::after {
        transform: rotate(315deg);
    }
}



/*  form  */

.input {
    border: 1px solid var(--osnovnoy---50);
    border-radius: 10px;
    padding: 16px 20px;
    background: #f0f5fa;
    color: var(--shrift---temnyy---100);
    transition: all 0.3s ease;
}
.input::placeholder {
    color: var(--shrift---temnyy---50);
}
.input:focus {
    border: 1px solid var(--osnovnoy---300);
}
.input:active {
    border: 1px solid var(--osnovnoy---300);
}

.input-wrapper {
    position: relative;
    & .input {
        width: 100%;
    }
}
.input.error {
    border: 1px solid var(--vtorostepennyy---krasnyy);
    background: rgba(225, 87, 63, 0.1);
}

input[type="email"], input[type="password"] {
    background: #f0f5fa;
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    -webkit-appearance:none;
}

.error-message {
    display: block;
    font-size: 12px;
    color: var(--vtorostepennyy---krasnyy);
    position: absolute;
    left: 4px;
    bottom: -22px;
}

.label {
    display: block;
    font-size: 14px;
    line-height: 140%;
    color: var(--shrift---temnyy---50);
    margin-bottom: 8px;
}

.label-pas.true {
    &::after {
        content: "";
        display: inline-block;
        height: 14px;
        width: 14px;
        background: url("./../img/icons/condition-true.svg") center / cover;
        margin-left: 6px;
        margin-bottom: -2px;
    }
}

.label-pas.false {
    color: var(--vtorostepennyy---krasnyy);
    &::after {
        content: "";
        display: inline-block;
        height: 14px;
        width: 14px;
        background: url("./../img/icons/condition-false.svg") center / cover;
        margin-left: 6px;
        margin-bottom: -2px;
    }
}


.pagination {
    display: flex;
    gap: 4px;
    & span {
        border: 1px solid var(--osnovnoy---100);
        border-radius: 8px;
        font-size: 14px;
        color: var(--shrift---temnyy---100);
        width: 32px;
        height: 32px;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all 0.3s ease;
        &.active {
            border: 1px solid var(--osnovnoy---50);
            background: var(--osnovnoy---50);
            color: var(--osnovnoy---500);
        }
        &.completed {
            border: 1px solid var(--vtorostepennyy---zelenyy);
            background: var(--vtorostepennyy---zelenyy);
            color: var(--shrift---belyy---100);
        }
    }
}


/*  search  */
.form-search {
    position: relative;
    width: 100%;
    & input {
        width: 100%;
    }
    &__btn {
        cursor: pointer;
        position: absolute;
        top: 50%;
        right: 16px;
        transform: translate(0, -50%);
        display: none;
    }
}


/* accordion */
.accordion {
    -webkit-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
    transition: all 0.4s ease;
    cursor: pointer;
    width: 100%;
    position: relative;

    &--active {
        .accordion__btn-more {
            transform: rotate(180deg);
        }
    }

    &__content {
        max-height: 0;
        overflow: hidden;
        -webkit-transition: all 0.4s ease;
        -o-transition: all 0.4s ease;
        transition: all 0.4s ease;
    }
}


/* tabs */
.tab-btns {
    display: flex;
    gap: 40px;
    border-bottom: 1px solid var(--osnovnoy---50);
}
.tab-btn {
    color: var(--shrift---temnyy---50);
    padding-bottom: 16px;
    border-bottom: 2px solid transparent;
    transition: all 0.3s ease;
    &:hover {
        color: var(--shrift---temnyy---100);
    }
    &.active {
        color: var(--shrift---temnyy---100);
        border-bottom: 2px solid var(--osnovnoy---500);
    }
}
.tab-content {
    display: none;
    &.active {
        display: block;
        width: 100%;
        min-height: 100%;
    }
}


code {
    margin-bottom: 20px;
    border-radius: 20px;
    & .tag span {
        border-radius: 0;
        border: none;
        padding: 0;
    }
    & .tag {
        padding: 0;
    }
}

.code-toolbar, pre {
    max-width: 1000px;
}

/*  media global  */
@media (max-width: 800px) {
    body {
        font-size: 14px;
    }
    .title-h1, .title-lk-h1 {
        font-size: 32px;
    }
    .title-h2, .title-lk-h2 {
        font-size: 28px;
    }
    .title-h3 {
        font-size: 24px;
    }
    .title-h4 {
        font-size: 22px;
    }
    .title-h5 {
        font-size: 18px;
    }
    .title-h6 {
        font-size: 16px;
        line-height: 125%;
    }
}


