.course {
    &__top {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        gap: 30px;
        padding-bottom: 32px;
        border-bottom: 1px solid var(--vtorostepennyy---400);
        margin-bottom: 32px;
    }

    &__content {
        display: flex;
        gap: 28px;
        padding-bottom: 32px;
        margin-bottom: 32px;
        border-bottom: 1px solid var(--vtorostepennyy---400);

        &_img {
            display: block;
            border-radius: 10px;
            min-width: 200px;
            max-width: 200px;
            height: 200px;
            overflow: hidden;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &_inner {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 30px;
        }

        &_text {
            & h1 {
                margin-bottom: 12px;
            }
            & p {
                color: var(--shrift---temnyy---50);
            }
        }

        &_infos {
            display: flex;
            justify-content: space-between;
            gap: 30px;
        }

        &_info {
            display: flex;
            gap: 8px;
            color: var(--shrift---temnyy---50);
            & span {
                color: var(--shrift---temnyy---100);
            }
            & img {
                display: block;
                min-width: 16px;
                margin-top: 3px;
            }
        }
    }

    &__list ol {
        display: flex;
        flex-direction: column;
        gap: 12px;
        list-style-type: none;
        counter-reset: item;

        & li {
            list-style-type: none;
        }

        & a {
            display: flex;
            gap: 12px;
            border: 1px solid rgba(0, 98, 199, 0.05);
            border-radius: 10px;
            padding: 24px;
            background: #f5f9ff;
            font-weight: 500;
            font-size: 18px;
            line-height: 140%;
            color: var(--shrift---temnyy---100);
            transition: all 0.3s ease;

            &::before {
                content: counter(item, decimal-leading-zero);
                counter-increment: item;
                display: block;
                font-weight: 700;
                font-size: 24px;
                line-height: 120%;
                color: var(--osnovnoy---100);
                transition: all 0.3s ease;
            }

            & span {
                display: block;
                padding-left: 12px;
                border-left: 1px solid var(--osnovnoy---50);
                width: 100%;
            }

            &:hover {
                border: 1px solid var(--osnovnoy---300);
                color: var(--osnovnoy---500);
                &::before {
                    color: var(--osnovnoy---500);
                }
            }

            &.process {
                border: 1px solid var(--osnovnoy---200);
                &::after {
                    content: "";
                    display: block;
                    min-width: 26px;
                    width: 26px;
                    height: 26px;
                    background: url("./../img/icons/course-process.svg") center / cover;
                }
            }

            &.completed {
                border: 1px solid var(--vtorostepennyy---zelenyy);
                &::before {
                    color: rgba(32, 202, 137, 0.5);
                }
                &::after {
                    content: "";
                    display: block;
                    min-width: 26px;
                    width: 26px;
                    height: 26px;
                    background: url("./../img/icons/course-completed.svg") center / cover;
                }
                &:hover {
                    border: 1px solid var(--vtorostepennyy---zelenyy);
                    color: var(--vtorostepennyy---zelenyy);
                    &::before {
                        color: var(--vtorostepennyy---zelenyy);
                    }
                }
            }
        }
    }




    &__card {
        border: 1px solid rgba(0, 98, 199, 0.05);
        border-radius: 20px;
        padding: 24px;
        background: #f5f9ff;
        display: flex;
        gap: 28px;


        &_inner {
            flex-grow: 1;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 20px;
        }

        &_top {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            gap: 20px;
        }

        &_items {
            display: flex;
            flex-direction: column;
            gap: 10px;
            font-size: 14px;
            line-height: 140%;
            color: var(--shrift---temnyy---50);

            & span {
                color: var(--shrift---temnyy---100);
            }
        }

        &_item {
            display: flex;
            gap: 8px;
            align-items: center;
            & img {
                min-width: 16px;
            }
        }
    }
}
