.test {
    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 30px;
        padding-bottom: 32px;
        border-bottom: 1px solid var(--vtorostepennyy---400);
        margin-bottom: 60px;

        &_count {
            color: var(--shrift---temnyy---50);
            & span {
                color: var(--shrift---temnyy---100);
            }
        }
    }

    &__list {
        margin-bottom: 56px;
        list-style-type: none;
        counter-reset: item;
    }

    &__item {
        &:not(:last-child) {
            padding-bottom: 50px;
            border-bottom: 1px solid var(--vtorostepennyy---400);
            margin-bottom: 50px;
        }

        &.hidden {
            clip: rect(0 0 0 0);
            clip-path: inset(50%);
            height: 1px;
            overflow: hidden;
            position: absolute;
            white-space: nowrap;
            width: 1px;
        }

        &_number {
            border-radius: 30px;
            padding: 10px 20px;
            background: var(--vtorostepennyy---500);
            color: var(--osnovnoy---500);
            width: fit-content;
            margin-bottom: 16px;
            display: flex;
            gap: 12px;
            
            &::before {
                content: "Вопрос "counter(item);
                counter-increment: item;
                font-size: 16px;
                color: var(--shrift---temnyy---50);
                padding-right: 12px;
                border-right: 1px solid #d3dbe4;
            }
        }

        &_question {
            font-size: 18px;
            line-height: 140%;
            color: var(--shrift---temnyy---100);
            margin-bottom: 40px;
        }

        &_text {
            font-weight: 700;
            font-size: 20px;
            line-height: 110%;
            color: var(--shrift---temnyy---100);
            margin-bottom: 24px;
        }

        &_answer {
            & label {
                display: flex;
                align-items: flex-start;
                gap: 10px;
                cursor: pointer;
                transition: 0.3s ease;
            }
            & ul {
                display: flex;
                flex-direction: column;
                gap: 16px;
                color: var(--shrift---temnyy---tekst-v-uroke);
            }

            & .checkbox {
                & label {
                    &::before {
                        content: "";
                        min-width: 20px;
                        width: 20px;
                        height: 20px;
                        outline: none;
                        border: 2px solid rgba(32, 202, 137, 0.3);
                        border-radius: 4px;
                        display: inline-block;
                        margin-top: 2px;
                        transition: 0.3s ease;
                    }
                
                    &:hover::before {
                        border-color: rgba(32, 202, 137, 0.7);
                    }
                }
                & input[type="checkbox"] {
                    display: none;
                }
                & input[type="checkbox"]:checked + label::before {
                    border-color: var(--vtorostepennyy---zelenyy);
                    background-color: var(--vtorostepennyy---zelenyy);
                    background-image: url("data:image/svg+xml;charset=UTF-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' width='18' height='18' fill='white'%3E%3Cpath d='M20.285 6.32a1.2 1.2 0 0 0-1.79-.085l-8.3 8.647-3.42-3.421a1.2 1.2 0 0 0-1.7 1.7l4.375 4.375a1.2 1.2 0 0 0 1.75-.043l9.15-9.15a1.2 1.2 0 0 0-.065-1.722z'/%3E%3C/svg%3E");
                    background-repeat: no-repeat;
                    background-position: center;
                }                
            }

            & .radio {
                & label {
                    &::before {
                        content: "";
                        min-width: 18px;
                        width: 18px;
                        height: 18px;
                        border-radius: 100%;
                        outline: 2px solid rgba(32, 202, 137, 0.3);
                        display: inline-block;
                        margin-top: 2px;
                        transition: 0.25s ease;
                    }
                
                    &:hover::before {
                        outline-color: rgba(32, 202, 137, 0.7);
                    }
                }
                & input[type="radio"] {
                    display: none;
                }
                & input[type="radio"]:checked + label::before {
                    outline: 2px solid var(--vtorostepennyy---zelenyy);
                    border: 4px solid #fff;
                    background-color: var(--vtorostepennyy---zelenyy);
                }
            }

            & textarea {
                border: 1px solid var(--osnovnoy---50);
                border-radius: 20px;
                padding: 24px;
                background: #f0f5fa;
                color: var(--shrift---temnyy---100);
                outline: none;
                width: 100%;
                min-height: 200px;
                resize: none;
                transition: all 0.3s ease;
            
                &::placeholder {
                    color: var(--shrift---temnyy---50);
                }
            
                &:focus, &:active {
                    border: 1px solid var(--osnovnoy---300);
                }
            }
        }
    }

    &__bottom {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 30px;

        &_next {
            border: 1px solid var(--vtorostepennyy---zelenyy);
            border-radius: 40px;
            padding: 8px 8px 8px 32px;
            color: var(--vtorostepennyy---zelenyy);
            font-size: 18px;
            display: flex;
            align-items: center;
            gap: 28px;
            transition: all 0.3s ease;

            & .finish-btn-ok {
                background: rgba(32, 202, 137, 0.1);
            } 

            &:hover {
                & .finish-btn-ok {
                    background: var(--vtorostepennyy---zelenyy);
                    & svg path {
                        fill: var(--shrift---belyy---100);
                    }
                } 
            }
        }
    }


    &__result {
        border-radius: 20px;
        padding: 60px;
        color: var(--shrift---belyy---100);
        display: flex;
        gap: 32px;
        margin: -60px;
        margin-bottom: 60px;
        
        &.positive {
            background: var(--vtorostepennyy---zelenyy);
        }

        &.negative {
            background: var(--vtorostepennyy---krasnyy);
        }

        &_icon {
            min-width: 140px;
            width: 140px;
            height: 140px;
        }

        &_text {
            padding-left: 32px;
            border-left: 1px solid var(--belyy---20);
        }

        &_title {
            font-weight: 700;
            font-size: 46px;
            line-height: 120%;
            margin-bottom: 16px;
        }

        &_subtitle {
            font-weight: 500;
            font-size: 18px;
        }


        &_item {
            & .test__item_number {
                position: relative;
                padding-left: 42px;
                &::before {
                    padding-right: 0;
                    border-right: none;
                    padding-left: 12px;
                    border-left: 1px solid #d3dbe4;
                }
                &::after {
                    content: "";
                    position: absolute;
                    top: 50%;
                    left: 11px;
                    transform: translate(0, -50%);
                    display: block;
                    min-width: 20px;
                    width: 20px;
                    height: 20px;
                }
            }
            &.positive .test__item_number::after {
                background: url("./../img/icons/positive.svg") no-repeat center / contain;
            }
            &.negative .test__item_number::after {
                background: url("./../img/icons/negative.svg") no-repeat center / contain;
            }

            & li {
                position: relative;
                width: fit-content;
                padding-right: 32px;
                &::after {
                    content: "";
                    position: absolute;
                    top: 50%;
                    right: 0;
                    transform: translate(0, -50%);
                    display: block;
                    min-width: 20px;
                    width: 20px;
                    height: 20px;
                }
                &.positive::after {
                    background: url("./../img/icons/positive.svg") no-repeat center / contain;
                }
                &.negative::after {
                    background: url("./../img/icons/negative.svg") no-repeat center / contain;
                }
            }
        }


    }
}


