
// login
.login {
    background: #f5f9ff;
    display: flex;
    gap: 10px;
    padding: 20px;
    height: 100vh;

    &__left {
        flex: 1 0 45%;
        padding: 40px;
        border-radius: 40px;
        position: relative;
        overflow: hidden;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 100%;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__logo {
        position: relative;
        z-index: 2;
        width: 100%;
        max-width: 248px;
        & img {
            width: 100%;
        }
    }

    &__right {
        flex: 1 0 45%;
        padding: 40px;
        border-radius: 40px;
        box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.05);
        background: var(--shrift---belyy---100);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    &__form {
        display: flex;
        flex-direction: column;
        gap: 32px;
        width: 100%;
        max-width: 511px;
    }

    &__title {
        margin-bottom: 80px;
    }
}