.graph {
    &__top {
        padding-bottom: 24px;
        border-bottom: 1px solid var(--vtorostepennyy---400);
        margin-bottom: 24px;
    }

    &__titles {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        margin-bottom: 52px;
    }

    &__profile {
        display: flex;
        align-items: center;
        gap: 16px;

        &_photo {
            display: block;
            min-width: 40px;
            width: 64px;
            height: 64px;
            border-radius: 100%;
            overflow: hidden;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &_name {
            font-size: 20px;
            line-height: 140%;
            color: var(--shrift---temnyy---100);
            margin-bottom: 4px;
        }

        &_post {
            font-size: 14px;
            color: var(--osnovnoy---500);
        }
    }

    &__tab {
        &_name {
            font-weight: 700;
            font-size: 24px;
            line-height: 120%;
            color: var(--shrift---temnyy---100);
            margin-bottom: 32px;
        }
    }
}
