.lesson {
    &__top {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        gap: 30px;
        padding-bottom: 32px;
        border-bottom: 1px solid var(--vtorostepennyy---400);
        margin-bottom: 32px;
    }

    &__bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 30px;
        margin-top: 56px;

        &_left {
            display: flex;
            align-items: center;
            gap: 24px;
            color: var(--shrift---temnyy---50);
            & span {
                color: var(--shrift---temnyy---100);
            }
        }
    }


    &__course {
        border-radius: 20px;
        padding: 24px;
        box-shadow: 0 4px 10px 0 rgba(0, 98, 199, 0.04);
        background: var(--shrift---belyy---100);

        &_img {
            display: block;
            border-radius: 10px;
            min-width: 64px;
            max-width: 64px;
            height: 64px;
            overflow: hidden;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &_top {
            display: flex;
            align-items: center;
            gap: 16px;
            padding-bottom: 16px;
            border-bottom: 1px solid var(--vtorostepennyy---400);
            margin-bottom: 16px;
            & span {
                display: block;
                font-size: 14px;
                line-height: 140%;
                color: var(--shrift---temnyy---50);
                margin-bottom: 4px;
            }
            & p {
                color: var(--shrift---temnyy---100);
            }
        }

        &_list p {
            display: block;
            font-size: 14px;
            line-height: 140%;
            color: var(--shrift---temnyy---50);
            margin-bottom: 12px;
        }

        &_list ol {
            display: flex;
            flex-direction: column;
            gap: 8px;
            list-style-type: none;
            counter-reset: item;
    
            & li {
                list-style-type: none;
            }
    
            & a {
                display: flex;
                align-items: center;
                gap: 8px;
                border: 1px solid rgba(0, 98, 199, 0.05);
                border-radius: 10px;
                padding: 12px;
                background: #f5f9ff;
                font-weight: 500;
                font-size: 14px;
                line-height: 140%;
                color: var(--shrift---temnyy---100);
                transition: all 0.3s ease;
    
                &::before {
                    content: counter(item, decimal-leading-zero);
                    counter-increment: item;
                    display: block;
                    font-weight: 700;
                    font-size: 14px;
                    line-height: 120%;
                    color: var(--osnovnoy---100);
                    transition: all 0.3s ease;
                }
    
                & span {
                    display: block;
                    padding-left: 8px;
                    border-left: 1px solid var(--osnovnoy---50);
                    width: 100%;
                }
    
                &:hover {
                    border: 1px solid var(--osnovnoy---300);
                    color: var(--osnovnoy---500);
                    &::before {
                        color: var(--osnovnoy---500);
                    }
                }
    
                &.process {
                    border: 1px solid var(--osnovnoy---200);
                    &::after {
                        content: "";
                        display: block;
                        min-width: 20px;
                        width: 20px;
                        height: 20px;
                        margin-left: 6px;
                        background: url("./../img/icons/course-process.svg") no-repeat center / contain;
                    }
                }
    
                &.completed {
                    border: 1px solid var(--vtorostepennyy---zelenyy);
                    &::before {
                        color: rgba(32, 202, 137, 0.5);
                    }
                    &::after {
                        content: "";
                        display: block;
                        min-width: 20px;
                        width: 20px;
                        height: 20px;
                        margin-left: 6px;
                        background: url("./../img/icons/course-completed.svg") no-repeat center / contain;
                    }
                    &:hover {
                        border: 1px solid var(--vtorostepennyy---zelenyy);
                        color: var(--vtorostepennyy---zelenyy);
                        &::before {
                            color: var(--vtorostepennyy---zelenyy);
                        }
                    }
                }
            }
        }
    }
}

@media(max-width: 1500px) {
    .lesson {
        &__bottom {
            flex-direction: column;
            align-items: flex-start;
            gap: 24px;
            &_left {
                justify-content: space-between;
                // flex-direction: column;
                // align-items: stretch;
                // gap: 12px;
            }
        }
    }
}
