.intro {
    position: relative;
    overflow: hidden;
    border-radius: 40px;
    padding: 50px 0 145px;
    margin: 20px;

    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 100%;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__header {
        position: relative;
        z-index: 2;
        margin-bottom: 84px;
        &_row {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 30px;
        }

        &_logo {
            padding: 16px 24px;
            border-radius: 40px;
            background: rgba(255, 255, 255, 0.1);
            transition: all 0.3s ease;
            &:hover {
                background: rgba(255, 255, 255, 0.2);
            }
        }

        &_btn {
            border-radius: 20px;
            padding: 20px 20px;
            font-weight: 500;
            font-size: 16px;
            line-height: 140%;
            color: var(--shrift---belyy---100);
            position: relative;
            transition: all 0.3s ease;
            margin-right: 64px;
        
            &:hover {
                background: var(--shrift---belyy---100);
                color: var(--osnovnoy---900);
            }
            
            &::before {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                border: 1px solid rgba(255, 255, 255, 0.8); 
                border-radius: 20px;
            
                --r: 22px;
                --w: calc(100% - 22px);
                --h: calc(62px - 22px);
                clip-path: polygon(var(--r) 0%, var(--r) var(--r), var(--w) var(--r), var(--w) 0%, 100% 0%, 100% var(--r), var(--w) var(--r), 
                var(--w) var(--h), 100% var(--h), 100% 100%, var(--w) 100%, var(--w) var(--h), var(--r) var(--h), var(--r) 100%, 0% 100%, 0% var(--h), var(--r) var(--h), var(--r) var(--r), 0% var(--r), 0% 0%);
                pointer-events: none; 
            }
        }
    }

    &__main {
        position: relative;
        z-index: 2;
        display: flex;
        align-items: center;
        gap: 70px;
    }

    &__text {
        color: var(--shrift---belyy---100);
        & h1 {
            font-size: 70px;
            color: var(--shrift---belyy---100);
            margin-bottom: 24px;
        }
        & p {
            font-size: 18px;
            line-height: 140%;
            color: var(--shrift---belyy---80);
            margin-bottom: 56px;
            max-width: 75%;
        }
        &_btn {
            border-radius: 20px;
            padding: 16px 16px 16px 28px;
            background: var(--shrift---belyy---100);
            border: 1px solid transparent;
            color: var(--osnovnoy---900);
            display: flex;
            gap: 11px;
            align-items: center;
            width: fit-content;
            transition: all 0.3s ease;

            & svg rect, & svg path {
                transition: all 0.3s ease;
            }

            &:hover {
                background: transparent;
                border: 1px solid var(--belyy---100);
                color: var(--shrift---belyy---100);
                & svg {
                    & rect {
                        fill: var(--belyy---100);
                    }
                    & path {
                        fill: var(--osnovnoy---500);
                    }
                }
            }
        }
    }

    &__cards {
        flex: 1 0 500px;
        display: flex;
        align-items: center;
        gap: 24px;

        & .swiper {
            max-height: 600px;
        }
    }

    &__card {
        border-radius: 20px;
        padding: 32px;
        background: var(--belyy---100);
        color: var(--shrift---temnyy---50);
        min-height: 189px;
        max-height: 189px;
        position: relative;

        & p:first-child {
            font-weight: 700;
            font-size: 24px;
            line-height: 110%;
            color: var(--osnovnoy---900);
            margin-bottom: 8px;
        }

        &_img {
            position: absolute;
            bottom: 0;
            right: 35px;
        }

        &::before {
            content: url("./../img/intro/card-bg.svg");
            position: absolute;
            bottom: 0;
            left: 0;
        }
    }

    &__swiper_btns {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 40px;
    }
}
