.popup-lesson {
    &__icon {
        display: block;
        width: fit-content;
        margin: 0 auto;
        margin-bottom: 28px;
    }

    &__content {
        overflow: hidden;
    }

    &__content h3 {
        font-weight: 700;
        font-size: 24px;
        line-height: 120%;
        text-align: center;
        color: var(--shrift---temnyy---100);
        margin-bottom: 40px;
    }

    &__result {
        border-radius: 10px;
        padding: 20px;
        background: #f0f5fa;
        margin-bottom: 40px;

        &_item {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            color: var(--shrift---temnyy---50);
            &:not(:last-child) {
                padding-bottom: 12px;
                border-bottom: 1px solid var(--osnovnoy---50);
                margin-bottom: 12px;
            }
            & span {
                font-size: 18px;
                color: var(--osnovnoy---900);
            }
        }
    }

    &__btns {
        display: flex;
        gap: 10px;
        & a, & button {
            flex: 1 0 40%;
        }
    }
}

.popup-positive {
    & .popup-lesson__result_item:first-child span {
        color: var(--vtorostepennyy---zelenyy);
    }
}

.popup-negative {
    & .popup-lesson__result_item:first-child span {
        color: var(--vtorostepennyy---krasnyy);
    }

    & .popup__content  {
        padding-top: 110px;
    }

    & .try-again {
        padding: 14px;
        background: var(--vtorostepennyy---zelenyy);
        color: var(--shrift---belyy---100);
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 10px;
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
    }
}