.footer {
    border-radius: 40px;
    padding: 80px 0;
    box-shadow: 0 0 10px 0 rgba(0, 98, 199, 0.04);
    background: var(--shrift---belyy---100);
    margin: 20px;
    margin-top: auto;

    &__inner {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        padding-bottom: 40px;
        border-bottom: 1px solid var(--vtorostepennyy---400);
        margin-bottom: 40px;
    }

    &__logo {
        display: block;
        margin-bottom: 24px;
    }

    &__info {
        font-size: 14px;
        line-height: 140%;
        color: var(--shrift---temnyy---50);
    }

    &__contact {
        flex-basis: 50%;
        display: flex;
        gap: 24px;

        &_text {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 20px;
            font-weight: 700;
            font-size: 20px;
            line-height: 110%;
            color: var(--shrift---temnyy---100);
        }

        &_link {
            border-radius: 200px;
            padding: 8px 16px 8px 8px;
            border: 1px solid transparent;
            width: fit-content;
            display: flex;
            align-items: center;
            gap: 10px;
            font-weight: 500;
            font-size: 16px;
            color: var(--shrift---temnyy---100);
            transition: all 0.3s ease;
            &:hover {
                border: 1px solid var(--osnovnoy---500);
            }
        }
    }


    &__bottom {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        font-size: 14px;
        color: var(--shrift---temnyy---100);
        & a {
            color: var(--shrift---temnyy---50);
            text-decoration: underline;
            transition: all 0.3s ease;
            &:hover {
                color: var(--shrift---temnyy---100);
            }
        }
    }

}