.popup-students {
    &__content {
        max-width: 960px;
        min-height: 788px;
    }

    &__content h3 {
        font-weight: 700;
        font-size: 40px;
        line-height: 120%;
        color: var(--shrift---temnyy---100);
        margin-bottom: 24px;
    }

    &__form {
        border-radius: 10px;
        padding: 2px;
        background: var(--osnovnoy---400);
        display: flex;
        gap: 4px;
        margin-bottom: 40px;
        
        &_btn {
            cursor: pointer;
            border-radius: 8px;
            padding: 16px 20px;
            color: var(--shrift---belyy---100);
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 8px;
            transition: all 0.3s ease;
            &:hover {
                background: var(--belyy---20);
            }
            &::after {
                content: url("./../img/icons/search.svg");
            }
        }
    }

    &__input {
        flex-grow: 1;
        height: 100%;
        border-radius: 8px;
        padding: 10px 16px;
        background: var(--belyy---100);
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        gap: 12px;
        max-height: 500px;
        overflow-y: auto;
        padding-right: 8px;
        &::-webkit-scrollbar {
            width: 4px;
        }
        &::-webkit-scrollbar-thumb {
            border-radius: 10px;
            background-color:  var(--osnovnoy---300);
        }
        &::-webkit-scrollbar-track {
            border-radius: 10px;
            background-color: #f0f5fa;
        }
    }
    
    &__item {
        border: 1px solid rgba(0, 98, 199, 0.05);
        border-radius: 10px;
        padding: 16px 24px 16px 16px;
        background: #f5f9ff;
        display: flex;
        align-items: center;
        gap: 16px;
        width: calc(50% - 6px);
        order: 2;
        &.none {
            display: none;
        }

        &_photo {
            border-radius: 40px;
            min-width: 56px;
            width: 56px;
            height: 56px;
            overflow: hidden;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &_text {
            flex-grow: 1;
            & p {
                font-size: 18px;
                line-height: 140%;
                color: var(--shrift---temnyy---100);
                margin-bottom: 4px;
            }
            & span {
                font-size: 14px;
                color: var(--osnovnoy---500);
            }
        }

        &.active {
            order: 1;
            & .popup-students__item_btn-add {
                display: none;
            }
            & .popup-students__item_condition {
                display: block;
            }
        }

        &_btn-add {
            & svg path, & svg rect {
                transition: all 0.3s ease;
            }
            &:hover svg {
                & rect {
                    stroke: var(--osnovnoy---500);
                }
                & path {
                    fill: var(--osnovnoy---500);
                }
            }
        }

        &_condition {
            position: relative;
            display: none;
        }

        &_btn-del, &_btn-active {
            transition: all 0.3s ease;
        }

        &_btn-active {
            position: absolute;
            top: 0;
            left: 0;
            &:hover {
                opacity: 0;
            }
        }
    }


}
