.courses {
    &__top {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        gap: 30px;
        margin-bottom: 52px;
    }

    &__filter {
        &_btns {
            display: flex;
            gap: 30px;
            border-bottom: 1px solid var(--osnovnoy---50);
        }

        &_btn {
            border-bottom: 2px solid transparent;
            padding: 0px 0px 16px 0px;
            color: var(--shrift---temnyy---50);
            display: flex;
            align-items: center;
            gap: 10px;
            transition: all 0.3s ease;

            &:hover {
                color: var(--shrift---temnyy---100);
                & .courses__filter_count {
                    background: var(--osnovnoy---50);
                    color: var(--osnovnoy---500);
                }
            }

            &.active {
                border-bottom: 2px solid var(--osnovnoy---500);
                color: var(--shrift---temnyy---100);
                & .courses__filter_count {
                    background: var(--osnovnoy---50);
                    color: var(--osnovnoy---500);
                }
            }
        }

        &_count {
            border-radius: 30px;
            background: var(--vtorostepennyy---400);
            font-size: 14px;
            text-align: center;
            color: var(--shrift---temnyy---50);
            width: 24px;
            height: 24px;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: all 0.3s ease;
        }
    }

    &__cards {
        display: flex;
        flex-direction: column;
        gap: 16px;
    }

    &__card {
        border: 1px solid rgba(0, 98, 199, 0.05);
        border-radius: 20px;
        padding: 24px;
        background: #f5f9ff;
        display: flex;
        gap: 28px;
        transition: all 0.3s ease;

        &:hover {
            border: 1px solid var(--osnovnoy---300);
            & .courses__card_title {
                color: var(--osnovnoy---500);
            }
        }

        &_img {
            display: block;
            border-radius: 10px;
            min-width: 210px;
            width: 210px;
            height: 210px;
            overflow: hidden;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &_inner {
            flex-grow: 1;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 20px;
        }

        &_top {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            gap: 20px;
        }
        
        &_title {
            transition: all 0.3s ease;
        }

        &_items {
            display: flex;
            flex-direction: column;
            gap: 10px;
            font-size: 14px;
            line-height: 140%;
            color: var(--shrift---temnyy---50);

            & span {
                color: var(--shrift---temnyy---100);
            }
        }

        &_item {
            display: flex;
            gap: 8px;
            align-items: center;
            & img {
                min-width: 16px;
            }
        }
    }
}

