@charset "UTF-8";
/* Reset and base styles  */
* {
  padding: 0px;
  margin: 0px;
  border: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Links */
a, a:link, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

/* Common */
aside, nav, footer, header, section, main {
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  font-size: inherit;
  font-weight: inherit;
}

ul, ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

img, svg {
  max-width: 100%;
  height: auto;
}

address {
  font-style: normal;
}

/* Form */
input, textarea, button, select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input::-ms-clear {
  display: none;
}

button, input[type=submit] {
  display: inline-block;
  box-shadow: none;
  background-color: transparent;
  background: none;
  cursor: pointer;
}

input:focus, input:active,
button:focus, button:active {
  outline: none;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

label {
  cursor: pointer;
}

legend {
  display: block;
}

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

main {
  display: block;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: 600;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
  display: block;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

:root {
  --container-width: 1560px;
  --container-padding: 20px;
  --font-family: "Manrope", sans-serif;
  --osnovnoy---900: #001f3f;
  --osnovnoy---500: #0062c7;
  --osnovnoy---400: #2c7dd1;
  --osnovnoy---300: #5798da;
  --osnovnoy---200: #83b3e4;
  --osnovnoy---100: #afceed;
  --osnovnoy---50: #dbe9f7;
  --vtorostepennyy---500: #f1f4fa;
  --vtorostepennyy---400: #e6ebf0;
  --shrift---belyy---100: #fff;
  --shrift---belyy---80: rgba(255, 255, 255, 0.8);
  --shrift---belyy---50: rgba(255, 255, 255, 0.5);
  --shrift---temnyy---100: #1a1a1a;
  --shrift---temnyy---50: #878da4;
  --belyy---100: #fff;
  --belyy---50: rgba(255, 255, 255, 0.5);
  --belyy---20: rgba(255, 255, 255, 0.2);
  --shrift---temnyy---25: #bfbfbf;
  --shrift---temnyy---tekst-v-uroke: #474c5e;
  --vtorostepennyy---zelenyy: #20ca89;
  --vtorostepennyy---fioletovyy: #a989ee;
  --vtorostepennyy---zheltyy: #fec400;
  --vtorostepennyy---krasnyy: #e1573f;
  --plashki: #424e59;
  --izobrazhenie: #d2dce8;
  --fon-plashek: #eaedf2;
  --temnyy: #080b0f;
  --tekst: #003649;
  --tekst-svetlyy: #405f6a;
  --belyy: #fff;
  --tegi: #556473;
  --bg-svetlo-seryy: #f9f9f9;
  --color-temno-zelenyy: #206175;
  --color-zelenyy: #3d7c85;
  --color-svetlo-zelenyy-1: #bedbd7;
  --color-svetlo-zelenyy-2: rgba(190, 219, 215, 0.4);
  --gradient-green: linear-gradient(222deg, rgba(106,165,174,1) 0%, rgba(61,124,133,1) 100%);
  --gradient-light-gray: linear-gradient(222deg, rgba(247,250,255,1) 0%, rgba(234,237,242,1) 100%);
  --gradient-for-photo-decor: linear-gradient(0deg, rgba(32,97,117,1) 0%, rgba(32,97,117,0) 30%, rgba(32,97,117,0) 100%);
  --decor-gray: #C7D6DC;
}

/* Manrope */
@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: url("../fonts/Manrope-Regular.woff2") format("woff2"), url("../fonts/Manrope-Regular.woff") format("woff");
}
@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-display: swap;
  font-weight: 500;
  src: url("../fonts/Manrope-Medium.woff2") format("woff2"), url("../fonts/Manrope-Medium.woff") format("woff");
}
@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-display: swap;
  font-weight: 600;
  src: url("../fonts/Manrope-Semibold.woff2") format("woff2"), url("../fonts/Manrope-Semibold.woff") format("woff");
}
@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-display: swap;
  font-weight: 700;
  src: url("../fonts/Manrope-Bold.woff2") format("woff2"), url("../fonts/Manrope-Bold.woff") format("woff");
}
/* Общие glob */
html.lock {
  overflow: hidden;
}

html.burger-lock {
  overflow: hidden;
}

html.burger-lock::before {
  opacity: 1;
}

html::before {
  display: block;
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3215686275);
  position: fixed;
  inset: 0;
  opacity: 0;
  z-index: 10;
  pointer-events: none;
  -webkit-transition: opacity 0.3s linear;
  -o-transition: opacity 0.3s linear;
  transition: opacity 0.3s linear;
}

.no-scroll {
  overflow: hidden;
}

body.lock {
  overflow: hidden;
}

body.burger-lock {
  overflow: hidden;
}

.page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

body {
  margin: 0;
  overflow-x: hidden;
  overflow-y: auto;
  padding: 0;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 140%;
  color: var(--shrift---temnyy---tekst-v-uroke);
  background: #f5f9ff;
}

body.lock {
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-padding);
  max-width: var(--container-width);
  width: 100%;
}

.dashboard {
  display: flex;
  align-items: flex-start;
  gap: 20px;
  margin-bottom: 160px;
}

.sidebar-wrap {
  max-width: 365px;
  min-height: 500px;
  border-radius: 20px;
  overflow: hidden;
  position: relative;
  transition: all 0.3s ease;
}
.sidebar-wrap__btn {
  display: none;
  position: absolute;
  bottom: 24px;
  left: 50%;
  z-index: 3;
  transform: translate(-50%, 0);
  transition: all 0.3s ease;
}
.sidebar-wrap__btn svg path {
  transition: all 0.3s ease;
}
.sidebar-wrap__btn:hover svg path {
  fill: var(--osnovnoy---500);
}
.sidebar-wrap__btn.rotate {
  display: block;
  transform: translate(-50%, 0) rotate(180deg);
}
.sidebar-wrap.hide::after {
  display: block;
}
.sidebar-wrap.hide .sidebar-wrap__btn {
  display: block;
  bottom: 24px;
}
.sidebar-wrap::after {
  content: "";
  display: none;
  width: 100%;
  height: 120px;
  background: linear-gradient(360deg, #fff 54.5%, rgba(255, 255, 255, 0.09) 100%);
  position: absolute;
  left: 0;
  bottom: 0;
}

.sidebar {
  border-radius: 20px;
  padding: 24px;
  box-shadow: 0 4px 10px 0 rgba(0, 98, 199, 0.04);
  background: var(--shrift---belyy---100);
  width: 100%;
  margin-bottom: 20px;
  max-width: 365px;
}
.sidebar__profile {
  display: flex;
  align-items: center;
  gap: 16px;
  padding-bottom: 16px;
  border-bottom: 1px solid var(--vtorostepennyy---400);
  margin-bottom: 16px;
}
.sidebar__profile_photo {
  display: block;
  min-width: 40px;
  width: 64px;
  height: 64px;
  border-radius: 100%;
  overflow: hidden;
}
.sidebar__profile_photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.sidebar__profile_name {
  font-size: 20px;
  line-height: 140%;
  color: var(--shrift---temnyy---100);
  margin-bottom: 4px;
}
.sidebar__profile_post {
  font-size: 14px;
  color: var(--osnovnoy---500);
}
.sidebar__menu_link {
  border-radius: 10px;
  padding: 14px 20px;
  font-weight: 500;
  color: var(--shrift---temnyy---100);
  display: flex;
  align-items: center;
  gap: 10px;
  transition: all 0.3s ease;
}
.sidebar__menu_link svg path {
  transition: all 0.3s ease;
}
.sidebar__menu_link:hover {
  color: var(--osnovnoy---500);
}
.sidebar__menu_link:hover svg path {
  fill: var(--osnovnoy---500);
}
.sidebar__menu_link.active {
  background: #f0f5fa;
  color: var(--osnovnoy---500);
}
.sidebar__menu_link.active svg path {
  fill: var(--osnovnoy---500);
}
.sidebar__menu_exit {
  padding-top: 16px;
  margin-top: 16px;
  border-top: 1px solid var(--vtorostepennyy---400);
}

.content {
  width: 100%;
  border-radius: 20px;
  padding: 60px;
  box-shadow: 0 4px 10px 0 rgba(0, 98, 199, 0.04);
  background: var(--shrift---belyy---100);
}

/*  titles  */
.title-h1, .title-h2, .title-h3, .title-h4, .title-h5, .title-h6, .title-lk-h1, .title-lk-h2 {
  font-family: var(--font-family);
  font-weight: 700;
  line-height: 120%;
  color: var(--shrift---temnyy---100);
}

.title-h1 {
  font-size: 70px;
}

.title-h2 {
  font-size: 52px;
}

.title-h3 {
  font-size: 32px;
}

.title-h4 {
  font-size: 24px;
}

.title-h5 {
  font-size: 20px;
}

.title-lk-h1 {
  font-size: 46px;
}

.title-lk-h2 {
  font-size: 40px;
}

/*  buttons */
.save-btn {
  display: block;
  border-radius: 40px;
  padding: 16px 24px;
  background: var(--osnovnoy---500);
  color: var(--shrift---belyy---100);
  transition: all 0.3s ease;
}
.save-btn:hover {
  background: rgba(0, 98, 199, 0.8);
}
.save-btn:active {
  transform: scale(0.95);
}
.save-btn:disabled {
  pointer-events: none;
  background: var(--osnovnoy---500);
  opacity: 0.5;
}

.finish-btn {
  border-radius: 40px;
  padding: 8px 8px 8px 32px;
  background: var(--vtorostepennyy---zelenyy);
  color: var(--shrift---belyy---100);
  font-size: 18px;
  display: flex;
  align-items: center;
  gap: 28px;
  transition: all 0.3s ease;
}
.finish-btn:hover {
  background: rgba(32, 202, 137, 0.8);
}
.finish-btn:active {
  transform: scale(0.95);
}

.finish-btn-ok {
  display: block;
  border-radius: 40px;
  padding: 10px;
  width: 48px;
  height: 48px;
  background: rgba(255, 255, 255, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}
.finish-btn-ok svg path {
  transition: all 0.3s ease;
}

.white-btn, .blue-btn {
  border-radius: 40px;
  padding: 16px 24px;
  text-align: center;
  transition: all 0.3s ease;
}

.white-btn {
  border: 1px solid var(--osnovnoy---500);
  color: var(--shrift---temnyy---100);
}
.white-btn:hover {
  color: var(--osnovnoy---500);
}

.blue-btn {
  background: var(--osnovnoy---500);
  color: var(--shrift---belyy---100);
}
.blue-btn:hover {
  background: rgba(0, 98, 199, 0.8);
}

.return-btn, .next-btn, .prev-btn {
  border: 1px solid var(--osnovnoy---50);
  border-radius: 40px;
  padding: 12px 24px;
  color: var(--shrift---temnyy---100);
  display: flex;
  align-items: center;
  gap: 12px;
  transition: all 0.3s ease;
}
.return-btn svg path, .next-btn svg path, .prev-btn svg path {
  transition: all 0.3s ease;
}

.return-btn {
  width: fit-content;
}
.return-btn:hover {
  border: 1px solid var(--osnovnoy---500);
  color: var(--osnovnoy---500);
}
.return-btn:hover svg path {
  fill: var(--osnovnoy---500);
}

.nav-btns {
  display: flex;
  gap: 12px;
}

.next-btn, .prev-btn {
  background: #f5f9ff;
}
.next-btn:hover, .prev-btn:hover {
  border: 1px solid var(--osnovnoy---500);
}
.next-btn.disabled, .prev-btn.disabled {
  pointer-events: none;
  opacity: 0.6;
}

.next-btn::after {
  content: "";
  display: block;
  background-image: url("./../img/icons/arrow-next.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  width: 18px;
  height: 16px;
}

.prev-btn::before {
  content: "";
  display: block;
  background-image: url("./../img/icons/arrow-prev.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  width: 18px;
  height: 16px;
}

/* tags */
.tag {
  border-radius: 40px;
  padding: 8px 16px 8px 8px;
  display: flex;
  align-items: center;
  gap: 10px;
  position: relative;
  width: fit-content;
  text-wrap: nowrap;
}
.tag::before {
  content: "";
  display: block;
  width: 26px;
  height: 26px;
}
.tag span {
  padding-left: 10px;
  border-left: 1px solid var(--belyy---20);
  text-wrap: nowrap;
}

.completed-tag, .new-tag, .process-tag {
  font-weight: 500;
  font-size: 14px;
  color: var(--shrift---belyy---100);
}

.completed-tag {
  background: var(--vtorostepennyy---zelenyy);
}
.completed-tag::before {
  background: url("./../img/icons/tag-completed.svg") no-repeat center/contain;
}

.new-tag {
  background: var(--vtorostepennyy---fioletovyy);
}
.new-tag::before {
  background: url("./../img/icons/tag-new.svg") no-repeat center/contain;
}

.process-tag {
  background: var(--osnovnoy---300);
}
.process-tag::before {
  background: url("./../img/icons/tag-process.svg") no-repeat center/contain;
}

.progress p {
  font-weight: 500;
  font-size: 14px;
  line-height: 140%;
  text-align: right;
  color: var(--shrift---temnyy---100);
  opacity: 0.5;
  text-wrap: nowrap;
  margin-bottom: 8px;
}

.progress-tag, .progress-tag-white {
  padding: 6px 12px 6px 6px;
  gap: 8px;
  font-size: 14px;
  color: var(--shrift---temnyy---50);
  margin-left: auto;
}
.progress-tag span, .progress-tag-white span {
  font-size: 18px;
  color: var(--shrift---temnyy---100);
}
.progress-tag__icon, .progress-tag-white__icon {
  padding-right: 8px;
  border-right: 1px solid var(--vtorostepennyy---500);
}

.progress-tag {
  background: var(--vtorostepennyy---500);
}
.progress-tag span {
  border-left: 1px solid rgba(0, 31, 63, 0.07);
}
.progress-tag::before {
  background: url("./../img/icons/tag-progress-1.svg") no-repeat center/contain;
}

.progress-tag-white {
  box-shadow: 0 4px 10px 0 rgba(0, 98, 199, 0.04);
  background: var(--shrift---belyy---100);
}
.progress-tag-white span {
  border-left: 1px solid var(--vtorostepennyy---500);
}
.progress-tag-white::before {
  background: url("./../img/icons/tag-progress.svg") no-repeat center/contain;
}

.btn-circle {
  display: block;
  border-radius: 200px;
  width: 34px;
  min-width: 34px;
  height: 34px;
  background: var(--osnovnoy---500);
  color: var(--belyy);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}
.btn-circle::after {
  content: url("./../img/icons/arrow-white.svg");
}
.btn-circle:hover {
  opacity: 0.5;
}

.btn-more.active::after {
  transform: rotate(315deg);
}

/*  form  */
.input {
  border: 1px solid var(--osnovnoy---50);
  border-radius: 10px;
  padding: 16px 20px;
  background: #f0f5fa;
  color: var(--shrift---temnyy---100);
  transition: all 0.3s ease;
}

.input::placeholder {
  color: var(--shrift---temnyy---50);
}

.input:focus {
  border: 1px solid var(--osnovnoy---300);
}

.input:active {
  border: 1px solid var(--osnovnoy---300);
}

.input-wrapper {
  position: relative;
}
.input-wrapper .input {
  width: 100%;
}

.input.error {
  border: 1px solid var(--vtorostepennyy---krasnyy);
  background: rgba(225, 87, 63, 0.1);
}

input[type=email], input[type=password] {
  background: #f0f5fa;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  -webkit-appearance: none;
}

.error-message {
  display: block;
  font-size: 12px;
  color: var(--vtorostepennyy---krasnyy);
  position: absolute;
  left: 4px;
  bottom: -22px;
}

.label {
  display: block;
  font-size: 14px;
  line-height: 140%;
  color: var(--shrift---temnyy---50);
  margin-bottom: 8px;
}

.label-pas.true::after {
  content: "";
  display: inline-block;
  height: 14px;
  width: 14px;
  background: url("./../img/icons/condition-true.svg") center/cover;
  margin-left: 6px;
  margin-bottom: -2px;
}

.label-pas.false {
  color: var(--vtorostepennyy---krasnyy);
}
.label-pas.false::after {
  content: "";
  display: inline-block;
  height: 14px;
  width: 14px;
  background: url("./../img/icons/condition-false.svg") center/cover;
  margin-left: 6px;
  margin-bottom: -2px;
}

.pagination {
  display: flex;
  gap: 4px;
}
.pagination span {
  border: 1px solid var(--osnovnoy---100);
  border-radius: 8px;
  font-size: 14px;
  color: var(--shrift---temnyy---100);
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}
.pagination span.active {
  border: 1px solid var(--osnovnoy---50);
  background: var(--osnovnoy---50);
  color: var(--osnovnoy---500);
}
.pagination span.completed {
  border: 1px solid var(--vtorostepennyy---zelenyy);
  background: var(--vtorostepennyy---zelenyy);
  color: var(--shrift---belyy---100);
}

/*  search  */
.form-search {
  position: relative;
  width: 100%;
}
.form-search input {
  width: 100%;
}
.form-search__btn {
  cursor: pointer;
  position: absolute;
  top: 50%;
  right: 16px;
  transform: translate(0, -50%);
  display: none;
}

/* accordion */
.accordion {
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
  cursor: pointer;
  width: 100%;
  position: relative;
}
.accordion--active .accordion__btn-more {
