.graph__tab_name {
  font-weight: 700;
  font-size: 24px;
  line-height: 120%;
  color: var(--shrift---temnyy---100);
  margin-bottom: 32px;
}

/* popups */
.popup {
  height: 100vh;
  left: 50%;
  opacity: 0;
  pointer-events: none;
  position: fixed;
  top: 50%;
  -webkit-transform: translate3d(-50%, -50%, 0);
  transform: translate3d(-50%, -50%, 0);
  width: 100%;
  z-index: 1000;
  -webkit-transition: opacity 0.4s ease;
  -o-transition: opacity 0.4s ease;
  transition: opacity 0.4s ease;
  overflow-y: auto;
  background: rgba(0, 0, 0, 0.7);
}

.popup__body {
  width: 100%;
  min-height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  padding: 20px;
}

.popup.open {
  opacity: 1;
  pointer-events: all;
}

.popup__content {
  position: relative;
  width: 100%;
  max-width: 600px;
  border-radius: 20px;
  padding: 60px;
  background: var(--shrift---belyy---100);
  -webkit-transition: -webkit-transform 0.3s linear;
  transition: -webkit-transform 0.3s linear;
  -o-transition: transform 0.3s linear;
  transition: transform 0.3s linear;
  transition: transform 0.3s linear, -webkit-transform 0.3s linear;
  -webkit-transform: rotateY(90deg);
  transform: rotateY(90deg);
}

.popup.open .popup__content {
  -webkit-transform: rotateY(0);
  transform: rotateY(0);
}

.close-popup {
  cursor: pointer;
  position: absolute;
  top: 0;
  right: -54px;
  z-index: 1000;
  width: 40px;
  height: 40px;
  -webkit-transition: -webkit-transform 0.4s linear;
  transition: -webkit-transform 0.4s linear;
  -o-transition: transform 0.4s linear;
  transition: transform 0.4s linear;
  transition: transform 0.4s linear, -webkit-transform 0.4s linear;
}

.close-popup img {
  height: 100%;
}

.close-popup:hover {
  transform: scale(1.1);
}

.none {
  display: none;
}

.popup-lesson__icon {
  display: block;
  width: fit-content;
  margin: 0 auto;
  margin-bottom: 28px;
}
.popup-lesson__content {
  overflow: hidden;
}
.popup-lesson__content h3 {
  font-weight: 700;
  font-size: 24px;
  line-height: 120%;
  text-align: center;
  color: var(--shrift---temnyy---100);
  margin-bottom: 40px;
}
.popup-lesson__result {
  border-radius: 10px;
  padding: 20px;
  background: #f0f5fa;
  margin-bottom: 40px;
}
.popup-lesson__result_item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  color: var(--shrift---temnyy---50);
}
.popup-lesson__result_item:not(:last-child) {
  padding-bottom: 12px;
  border-bottom: 1px solid var(--osnovnoy---50);
  margin-bottom: 12px;
}
.popup-lesson__result_item span {
  font-size: 18px;
  color: var(--osnovnoy---900);
}
.popup-lesson__btns {
  display: flex;
  gap: 10px;
}
.popup-lesson__btns a, .popup-lesson__btns button {
  flex: 1 0 40%;
}

.popup-positive .popup-lesson__result_item:first-child span {
  color: var(--vtorostepennyy---zelenyy);
}

.popup-negative .popup-lesson__result_item:first-child span {
  color: var(--vtorostepennyy---krasnyy);
}
.popup-negative .popup__content {
  padding-top: 110px;
}
.popup-negative .try-again {
  padding: 14px;
  background: var(--vtorostepennyy---zelenyy);
  color: var(--shrift---belyy---100);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.popup-students__content {
  max-width: 960px;
  min-height: 788px;
}
.popup-students__content h3 {
  font-weight: 700;
  font-size: 40px;
  line-height: 120%;
  color: var(--shrift---temnyy---100);
  margin-bottom: 24px;
}
.popup-students__form {
  border-radius: 10px;
  padding: 2px;
  background: var(--osnovnoy---400);
  display: flex;
  gap: 4px;
  margin-bottom: 40px;
}
.popup-students__form_btn {
  cursor: pointer;
  border-radius: 8px;
  padding: 16px 20px;
  color: var(--shrift---belyy---100);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  transition: all 0.3s ease;
}
.popup-students__form_btn:hover {
  background: var(--belyy---20);
}
.popup-students__form_btn::after {
  content: url("./../img/icons/search.svg");
}
.popup-students__input {
  flex-grow: 1;
  height: 100%;
  border-radius: 8px;
  padding: 10px 16px;
  background: var(--belyy---100);
}
.popup-students__list {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  max-height: 500px;
  overflow-y: auto;
  padding-right: 8px;
}
.popup-students__list::-webkit-scrollbar {
  width: 4px;
}
.popup-students__list::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: var(--osnovnoy---300);
}
.popup-students__list::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: #f0f5fa;
}
.popup-students__item {
  border: 1px solid rgba(0, 98, 199, 0.05);
  border-radius: 10px;
  padding: 16px 24px 16px 16px;
  background: #f5f9ff;
  display: flex;
  align-items: center;
  gap: 16px;
  width: calc(50% - 6px);
  order: 2;
}
.popup-students__item.none {
  display: none;
}
.popup-students__item_photo {
  border-radius: 40px;
  min-width: 56px;
  width: 56px;
  height: 56px;
  overflow: hidden;
}
.popup-students__item_photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.popup-students__item_text {
  flex-grow: 1;
}
.popup-students__item_text p {
  font-size: 18px;
  line-height: 140%;
  color: var(--shrift---temnyy---100);
  margin-bottom: 4px;
}
.popup-students__item_text span {
  font-size: 14px;
  color: var(--osnovnoy---500);
}
.popup-students__item.active {
  order: 1;
}
.popup-students__item.active .popup-students__item_btn-add {
  display: none;
}
.popup-students__item.active .popup-students__item_condition {
  display: block;
}
.popup-students__item_btn-add svg path, .popup-students__item_btn-add svg rect {
  transition: all 0.3s ease;
}
.popup-students__item_btn-add:hover svg rect {
  stroke: var(--osnovnoy---500);
}
.popup-students__item_btn-add:hover svg path {
  fill: var(--osnovnoy---500);
}
.popup-students__item_condition {
  position: relative;
  display: none;
}
.popup-students__item_btn-del, .popup-students__item_btn-active {
  transition: all 0.3s ease;
}
.popup-students__item_btn-active {
  position: absolute;
  top: 0;
  left: 0;
}
.popup-students__item_btn-active:hover {
  opacity: 0;
}