.advantages {
    padding: 160px 0;
    position: relative;
    overflow: hidden;

    & .container {
        position: relative;
    }

    &__bg {
        display: block;
        position: absolute;
        top: 30%;
        left: 0;
        right: 0;
        z-index: -1;
        width: 100%;
        transform: translate(0, -50%);
        & img {
            width: 100%;
            object-fit: contain;
        }
    }

    &__main {
        max-width: 960px;
        margin: 0 auto;
        margin-bottom: 80px;
    }

    &__title {
        text-align: center;
        margin-bottom: 56px;
        position: relative;
        z-index: 2;
        & span {
            color: var(--osnovnoy---500);
        }
    }

    &__img {
        max-width: 360px;
        margin: 0 auto;
        overflow: hidden;
        position: relative;
        &_picture  {
            & img {
                display: block;
                border-radius: 20px;
                overflow: hidden;
            }
        }
    }

    &__swiper-img {
        width: calc(100% - 30px);
        height: calc(100% - 30px);
        position: absolute;
        top: 15px;
        left: 15px;
        right: 15px;
        bottom: 15px;
        border-radius: 6px;
        //transform: translate(-50%, -50%);
        &_slide img {
        }
        
    }

    &__icon {
        position: absolute;
        &:nth-child(1) {
            top: 20px;
            left: 120px;
        }
        &:nth-child(2) {
            bottom: -17px;
            left: 0;
        }
        &:nth-child(3) {
            top: 82px;
            right: 75px;
        }
        &:nth-child(4) {
            right: 0;
            bottom: -120px;
        }
    }

    &__swiper-wrap {
        padding: 24px 0;
        background: var(--vtorostepennyy---500);
        overflow: hidden;
    }

    &__swiper {
        overflow: visible;
    }

    &__swiper-slide {
        border-radius: 20px;
        padding: 24px;
        transition: all 0.4s ease;

        &.swiper-slide-active {
            background: var(--osnovnoy---500);
            & .advantages__card {
                color: var(--shrift---belyy---80);
                background:
                    linear-gradient(to right, var(--belyy---100) 1px, transparent 1px) 0 0,
                    linear-gradient(to right, var(--belyy---100) 1px, transparent 1px) 0 100%,
                    linear-gradient(to left, var(--belyy---100) 1px, transparent 1px) 100% 0,
                    linear-gradient(to bottom, var(--belyy---100) 1px, transparent 1px) 0 0,
                    linear-gradient(to bottom, var(--belyy---100) 1px, transparent 1px) 100% 0,
                    linear-gradient(to top, var(--belyy---100) 1px, transparent 1px) 0 100%;
                background-repeat: no-repeat;
                background-size: 10px 10px;
                &::before {
                    border: 1px solid var(--belyy---100);
                    background-color: var(--belyy---100);
                }
                & p:first-child {
                    color: var(--shrift---belyy---100);
                }
            }
        }
    }

    &__card {
        padding: 22px;
        color: var(--shrift---temnyy---50);
        position: relative;
        background:
            linear-gradient(to right, var(--osnovnoy---200) 1px, transparent 1px) 0 0,
            linear-gradient(to right, var(--osnovnoy---200) 1px, transparent 1px) 0 100%,
            linear-gradient(to left, var(--osnovnoy---200) 1px, transparent 1px) 100% 0,
            linear-gradient(to bottom, var(--osnovnoy---200) 1px, transparent 1px) 0 0,
            linear-gradient(to bottom, var(--osnovnoy---200) 1px, transparent 1px) 100% 0,
            linear-gradient(to top, var(--osnovnoy---200) 1px, transparent 1px) 0 100%;
        background-repeat: no-repeat;
        background-size: 10px 10px;
        transition: all 0.4s ease;

        &::before {
            content: "";
            display: block;
            position: absolute;
            right: 0;
            bottom: 0;
            width: 10px;
            height: 10px;
            border-radius: 100%;
            border: 1px solid var(--osnovnoy---200);
            transition: all 0.4s ease;
        }
        & p:first-child {
            font-weight: 700;
            font-size: 24px;
            line-height: 110%;
            color: var(--osnovnoy---900);
            margin-bottom: 16px;
            transition: all 0.4s ease;
        }
    }
}