// animation
@keyframes transformItem {
    0% {
        opacity: 0;
        transform: translateY(100%);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}
.transform-item {
    opacity: 0;
}

.scale-item {
    scale: 0.6;
    opacity: 0;
    transition: scale .4s ease, opacity .3s ease;
}

