@keyframes textAnimation {
    0% {
        opacity: 0;
        transform: translateX(-120px);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}
.text-animation {
    opacity: 0;
}

.animation-active {
    &.scale-item {
        opacity: 1;
        scale: 1;
        &:nth-child(2n) {
            transition: scale .6s ease, opacity .5s ease;
        }
        &:nth-child(3n) {
            transition: scale .9s ease, opacity .8s ease;
        }
        &:nth-child(5n) {
            transition: scale .7s ease, opacity .6s ease;
        }
        &:nth-child(7n) {
            transition: scale .8s ease, opacity .7s ease;
        }
    }
    // &.rotate-item {
    //     transform: rotateY(0);
    //     opacity: 1;
    // }
    &.text-animation  {
        opacity: 1;
        animation: textAnimation .4s forwards;
    }
    &.transform-item {
        opacity: 1;
        animation: transformItem .4s forwards;
        &:nth-child(2n) {
            animation: transformItem .6s forwards;
        }
        &:nth-child(3n) {
            animation: transformItem .7s forwards;
        }
        &:nth-child(5n) {
            animation: transformItem .8s forwards;
        }
        &:nth-child(7n) {
            animation: transformItem .9s forwards;
        }
    }
}


/* animation photo */
.img-animation img {
    -webkit-transition: scale 3s ease;
    -o-transition: scale 3s ease;
    transition: scale 3s ease;
}

.img-animation {
    overflow: hidden;
    position: relative;
    border-radius: 20px;
    &:hover img {
        scale: 1.08;
    }

    // one option to choose

    // option-1
    &::before {
        content: "";
        background:  rgba(8, 11, 15, 0.2);
        position: absolute;
        z-index: 2;
        padding: 0.85em 0.75em;
        display: block;
        left: -2%;
        right: -2%;
        top: 0;
        bottom: -2px;
        transform: skewX(-45deg) scale(0, 1);
        transition: all 0.5s ease;
    }
    &:hover::before {
        transform: none;
    }


