
// statistic
.statistic {
    &__top {
        padding-bottom: 24px;
        border-bottom: 1px solid var(--vtorostepennyy---400);
        margin-bottom: 24px;
    }

    &__titles {
        margin-bottom: 52px;
        &_text {
            font-size: 18px;
            line-height: 140%;
            color: var(--osnovnoy---500);
            margin-bottom: 10px;
            display: flex;
            align-items: center;
            gap: 8px;
            &::after {
                content: url("./../img/icons/graph-blue.png");
            }
        }
    }

    &__filter {
        display: flex;
        gap: 24px;
        padding-bottom: 24px;
        border-bottom: 1px solid var(--vtorostepennyy---400);
        margin-bottom: 24px;
        &_select {
            width: 100%;
            max-width: 340px;
            & select {
                border: 1px solid var(--osnovnoy---50);
                border-radius: 10px;
                padding: 16px 20px;
                color: var(--shrift---temnyy---100);
                width: 100%;
                outline: none;
                padding-right: 30px;
                -webkit-appearance: none;
                -moz-appearance: none;
                -ms-appearance: none;
                appearance: none;
                background: #f0f5fa url(./../img/icons/arrow-bottom.svg) no-repeat right;
                background-position-x: calc(100% - 16px);
                transition: all 0.3s ease;
                &:hover, &:focus, &:active {
                    border: 1px solid var(--osnovnoy---300);
                }
            }
            & label {
                display: block;
                font-size: 14px;
                line-height: 140%;
                color: var(--shrift---temnyy---50);
                margin-bottom: 8px;
            }
        }
    }

    &__table {
        width: 100%;
        overflow: hidden;
        border-collapse: collapse; 
        display: block;

        & thead {
            border-radius: 10px;
            display: block;
            width: 100%;
            & tr {
                display: flex;
                width: 100%;
            }
            & th {
                &:nth-child(1) {width:100%; max-width: 22%;}
                &:nth-child(2) {width:100%; max-width: 13%;}
                &:nth-child(3) {width:100%; max-width: 15%;}
                &:nth-child(4) {width:100%; max-width: 12%;}
                &:nth-child(5) {width:100%; max-width: 15%;}
                &:nth-child(6) {width:100%; max-width: 15%;}
                &:nth-child(7) {width:100%; max-width: 8%;}
            }
            & th {
                border: none;
                padding: 16px 10px;
                background: #f0f5fa;
                font-weight: 500;
                font-size: 14px;
                line-height: 140%;
                text-align: left;
                color: var(--shrift---temnyy---50);
                &:first-child {
                    border-radius: 10px 0 0 10px;
                    padding-left: 16px;
                }
                &:last-child {
                    border-radius: 0 10px 10px 0;
                    padding-right: 16px;
                }
            }
        }
    }

    & tbody {
        display: block;
        width: 100%;
        & tr {
            display: flex;
            align-items: center;
            width: 100%;
            border-bottom: 1px solid var(--osnovnoy---50);
        }
        & td {
            &:nth-child(1) {width:100%; max-width: 22%;}
            &:nth-child(2) {width:100%; max-width: 13%;}
            &:nth-child(3) {width:100%; max-width: 15%;}
            &:nth-child(4) {width:100%; max-width: 12%;}
            &:nth-child(5) {width:100%; max-width: 15%;}
            &:nth-child(6) {width:100%; max-width: 15%;}
            &:nth-child(7) {width:100%; max-width: 8%;}
        }
        & td {
            border: none;
            padding: 16px 10px;
            // border-bottom: 1px solid var(--osnovnoy---50);
            font-size: 14px;
            color: var(--shrift---temnyy---tekst-v-uroke);
            &:first-child {
                padding-left: 16px;
                line-height: 120%;
            }
            &:last-child {
                padding: 16px;
                position: relative;
                &::before {
                    content: "";
                    display: block;
                    width: 1px;
                    height: calc(100% - 32px);
                    background: var(--osnovnoy---50);
                    position: absolute;
                    top: 16px;
                    left: 0;
                }
            }
            & p {
                color: var(--shrift---temnyy---100);
                margin-bottom: 5px;
            }
            & span {
                display: block;
                font-size: 12px;
                color: var(--osnovnoy---500);
            }
            &.progress, &.finished, &.process, &.not-started {
                &::before {
                    content: "";
                    display: inline-block;
                    width: 16px;
                    height: 16px;
                    margin-right: 8px;
                    margin-top: -2px;
                    vertical-align: middle;
                }
            }
            &.progress::before {
                width: 18px;
                height: 18px;
                background: url("./../img/icons/tag-progress.svg") no-repeat center / contain;
            }
            &.finished::before {
                background: url("./../img/icons/condition-true.svg") no-repeat center / contain;
            }
            &.process::before {
                background: url("./../img/icons/condition-time.svg") no-repeat center / contain;
            }
            &.not-started::before {
                background: url("./../img/icons/condition-false.svg") no-repeat center / contain;
            }

            & a {
                border-radius: 40px;
                border: 1px solid var(--osnovnoy---100);
                width: 40px;
                height: 40px;
                display: flex;
                align-items: center;
                justify-content: center;
                transition: all 0.3s ease;
                &:hover {
                    border: 1px solid var(--osnovnoy---500);
                }
            }
        }
    }
}